import { Component } from '@angular/core';
import {Router} from "@angular/router";
import {Http, Headers, RequestOptions} from "@angular/http";

@Component({
  selector: 'app-forget',
  template: require('./forget.component.html'),
  styles: [require('./forget.component.scss')]
})
export class ForgetComponent {
  user = {
    "email" : ""
  };

  constructor(private http: Http,  private router: Router){

  }

  forget(){

    this.http.post("http://test.joinmyevent.com/ems/ws/register/user/forgotPassword", this.user).subscribe((data) => {
      let res = data.json();
      if(res.status.errorCode !== 402){
        this.router.navigate(["/"]);
      }
      console.log(res)
    });
  }
}



